import React, { useState, useEffect } from 'react';
import { Button, Modal, Form } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { ref, set, getDatabase, get, child } from "firebase/database";
import app from '../../../firebase/firebase';

import './TeacherFlashcards.css';

const TeacherFlashcards = () => {

    const database = getDatabase(app);
    const currentDate = new Date();

    const navigate = useNavigate();

    const [numDbCards, setNumDbCards] = useState(0);
    const [CSVfile, setCSVFile] = useState();
    const [dataArray, setDataArray] = useState([]); //this is the array containing CSV data to be processed
    const [isSelected, setIsSelected] = useState(false);
    const [isImported, setIsImported] = useState(false);
    const [isUploaded, setIsUploaded] = useState(false);
    // for naming inputs
    const [inputSetName, setInputSetName] = useState("");
    const [inputSetAuthor, setInputSetAuthor] = useState("");
    // for modal
    const [showModal, setShowModal] = useState(false);
    // for spinner
    const [isLoading, setIsLoading] = useState(false);

  
    useEffect(()=>{
        const fetchData = async () => {
            const dbRef = ref(getDatabase());
            await get(child(dbRef, 'Flashcards/'))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    let queryData = snapshot.val();
                    const numPreexistingCards = Object.keys(queryData).length
    
                    console.log("There are currently " + numPreexistingCards + " sets currently in the flashcard library.")
                    setNumDbCards(numPreexistingCards); //find out how many cards are already in the db
                    setIsLoading(false);
                    
                } else {
                    console.log("No Data Available")
                }
            })
            .catch((error) => {
                console.error(error);
            })
        }
        //call the API
        fetchData();
    }, [database]);


    const fileReader = new FileReader();
  
    const handleOnChange = (e) => {
        setCSVFile(e.target.files[0]);
        setIsSelected(true);
    };
  
    const csvFileToArray = string => {
      const csvHeader = string.slice(0, string.indexOf("\n")).split(",");
      const csvRows = string.slice(string.indexOf("\n") + 1).split("\n");
      console.log("How many values in HeadeR:");
      console.log(csvHeader.length);
      ////// THIS WAS WHAT PARSED BEFORE
      const array = csvRows.map(i => {
        const values = i.split(",");         
        const obj = csvHeader.reduce((object, header, index) => {
          //2023-05-29 - this if clause was added to deal with splitting the example sentences if there was a comma in the example.
          //           - if the number of headers change, we need to change the number hard-coded @ (index === 3), as the example sentence is the 3rd and final value in the CSV file
          if (index === 3) {
            object[header] = values.slice(3).join(',');
          }
          else {
            object[header] = values[index];
          }
          return object;
        }, {});

        return obj;
      });

      setDataArray(array);
    };
  
    const handleOnSubmit = (e) => {
      e.preventDefault();
  
      if (CSVfile) {
        fileReader.onload = function (event) {
          const text = event.target.result;
          setIsImported(true);
          csvFileToArray(text);
        };
  
        fileReader.readAsText(CSVfile);
      }
      else {
        console.log("Choose a file first!")
      }
    };
  
    const headerKeys = Object.keys(Object.assign({}, ...dataArray));

    const handlePrematureClick = () => {
        console.log("Upload CSV file before trying to submit")
    };

    // const handleNav = () => {
    //     navigate("/FlashcardLibrary")
    // };

    const submitData = () => {
        handleModalOpen();
    };

    const handleModalClose = () => setShowModal(false);
    const handleModalOpen = () => setShowModal(true);

    const handleNameText = (e) => {
        setInputSetName(e.target.value);
    };

    const handleAuthorText = (e) => {
        setInputSetAuthor(e.target.value);
    };

    const flashcardSetNumberHelper = (n) => {
        if (n > 9999) {
            console.log("n is greater than or equal to 10,000. Scale your webapp")
        }
        else {
            //I'm arbitrarily setting the maximum number of sets in the library to 10,000. Firebase free usage dictates 1Gb limit
            const numDigits = n.toString().length;
            if (numDigits < 4) {
                const numForDB = n.toString().padStart(4,"0");
                return numForDB;
            }
            else {
                return n.toString();
            }

        }
    };

    const handleFinalSubmit = () => {
        setIsLoading(true);
        const newSetNum = numDbCards;
        let newSetCards = {};

        for (let i = 0; i < dataArray.length-1; i++) {
                newSetCards = {...newSetCards,
                    ["card" + i] : {
                        "Front" : dataArray[i][headerKeys[0]],
                        "Back" : dataArray[i][headerKeys[1]],
                        "WordType" : dataArray[i][headerKeys[2]],
                        "Example" : dataArray[i][headerKeys[3]]
                    }
                };
        };

        let creationTimeStamp = 
            currentDate.getFullYear() + '-' +
            currentDate.getMonth()    + 1 + '-' + 
            currentDate.getDay()      + '-' + 
            currentDate.getHours()    + '-' + 
            currentDate.getMinutes()  + '-' + 
            currentDate.getSeconds()

        let creation = {};

        creation[creationTimeStamp] = 'Created';

        let newDataToUpload = {
            "SetAuthor" : inputSetAuthor,
            "SetName" : inputSetName,
            "SetCards" : newSetCards, 
            "SetCompletions" : creation
        };

        // console.dir(newDataToUpload)

        // send data to Firebase
        console.log("Padded Values: " + flashcardSetNumberHelper(newSetNum))
        const refSection = ref(database, ('Flashcards/Set' + flashcardSetNumberHelper(newSetNum)));
        set(refSection, newDataToUpload)
        .then(()=>{
            setIsLoading(false);
            setIsUploaded(true);
            // navigate("/FlashcardLibrary");
        });
        
    };

    const handleFinalNavigate = () => {
        navigate("/FlashcardLibrary");
    };

    return (
        <div className="TeacherFlashcardsContainer" style={{ textAlign: "center" }}>
            
            <div className="TeacherFlashcardTitles">
                <div><b>Manage Flashcards</b></div>
                <div><i>Import CSV to Flashcard</i></div>
            </div>
            
            <div className="TeacherFlashcardsForm">
                <form>
                    <input
                        style={{ margin: "25px 0px 25px 75px", textAlign: "center" }}
                        type={"file"}
                        id={"csvFileInput"}
                        accept={".csv"}
                        onChange={handleOnChange}
                    />
                </form>
                
                <div className="TeacherFlashcardsButtons">
                    <div className="TeacherFlashcardsNoUploadButtons">
                        {isSelected
                            ?   <Button
                                    style={{ marginRight: "15px"}}
                                    variant="secondary"
                                    onClick={(e) => {
                                        handleOnSubmit(e);
                                    }}
                                    >
                                    IMPORT CSV
                                </Button>
                            :   <Button
                                    style={{ marginRight: "15px"}}
                                    variant="outline-secondary"
                                    onClick={(e) => {
                                        handleOnSubmit(e);
                                    }}
                                    >
                                    IMPORT CSV
                                </Button>
                        }
                        
                        {isImported
                            ?   <>
                                <Button 
                                    style={{ marginLeft: "15px"}} 
                                    variant="success"
                                    onClick={submitData}
                                >    
                                    Upload
                                </Button>

                                <Modal
                                    show={showModal}
                                    onHide={handleModalClose}
                                    backdrop="static"
                                    keyboard={false}
                                >
                                    <Modal.Header>
                                        <Modal.Title>Flashcard Set Names</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <div style={{ marginBottom: "15px", fontSize: "large" }}>
                                            <b>Please give this flashcard set a name and author</b>
                                        </div>
                                        <Form>
                                            <Form.Group className="mb-3" controlId="formSetName">
                                                <Form.Label>Flashcard Set Name</Form.Label>
                                                {isUploaded
                                                    ? 
                                                        <Form.Control type="text" placeholder="Enter set name" onChange={handleNameText} disabled />
                                                    : 
                                                        <Form.Control type="text" placeholder="Enter set name" onChange={handleNameText}/>
                                                }
                                                <Form.Text className="text-muted">
                                                    This will be diplayed on a card in the Flascard library
                                                </Form.Text>
                                            </Form.Group>
                                            <Form.Group className="mb-3" controlId="formSetAuthor">
                                                <Form.Label>Flashcard Set Author</Form.Label>
                                                {isUploaded
                                                    ? 
                                                        <Form.Control type="text" placeholder="Enter author name/ID" onChange={handleAuthorText} disabled />
                                                    : 
                                                        <Form.Control type="text" placeholder="Enter author name/ID" onChange={handleAuthorText} />
                                                }
                                                <Form.Text className="text-muted">
                                                    Please write a name students will recognize
                                                </Form.Text>
                                            </Form.Group>
                                        </Form>
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <div className="ModalFooter">
                                            <div className="spinner">
                                                {isLoading 
                                                    ? (
                                                    <div className="ModalLoader">
                                                        <div className="ModalInner one"></div>
                                                        <div className="ModalInner two"></div>
                                                        <div className="ModalInner three"></div>
                                                    </div>
                                                    ) 
                                                    :   null 
                                                }
                                            </div>
                                            <div className="ModalButtons">
                                                {isUploaded
                                                    ?   
                                                        <div>
                                                            <Button variant="primary" onClick={handleFinalNavigate}>
                                                                Flashcard Library
                                                            </Button>
                                                        </div>
                                                    :   
                                                        <div>
                                                            <Button variant="secondary" onClick={handleModalClose} style={{ marginRight: "5px" }}>
                                                                Close
                                                            </Button>
                                                            {inputSetName.length > 0 && inputSetAuthor.length > 0
                                                                ?   <Button variant="success" onClick={handleFinalSubmit} style={{ marginLeft: "5px" }}>Submit</Button>
                                                                :   <Button variant="outline-danger" style={{ marginLeft: "5px" }}>Submit</Button>
                                                            }
                                                        </div>
                                                }
                                                
                                            </div>
                                        </div>
                                    </Modal.Footer>
                                </Modal>
                                </>
                            :   <Button 
                                    style={{ marginLeft: "15px"}} 
                                    variant="outline-success"
                                    onClick={handlePrematureClick}
                                >
                                    Upload
                                </Button>


                        }

                    </div>
                </div>    
            </div>
    
            <br />

            <div className="TeacherFlashcardDataInstructions">
                {isImported
                    ?
                        <div style={{ margin: "5px 0px 25px 0px" }}>---------- Please review your data ----------</div>
                    :        
                        null
                }
            </div>

            <div className="TeacherFlashcardsDataTable">        
                <table>
                    <thead>
                    <tr>
                        {headerKeys.map((key, idx1) => (
                        <th key={idx1}>{key}</th>
                        ))}
                    </tr>
                    </thead>
            
                    <tbody>
                    {dataArray.map((item, idx2) => (
                        <tr key={idx2}>
                        {Object.values(item).map((val, idx3) => (
                            <td key={idx3}>{val}</td>
                        ))}
                        </tr>
                    ))}
                    </tbody>
                </table>
            </div>
        </div>
      );

}

export default TeacherFlashcards;