import React, { useState, useEffect, createRef } from 'react';
import { useNavigate } from 'react-router-dom';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import { Modal, Form, InputGroup, Button } from 'react-bootstrap';
// import { SiAudiomack } from 'react-icons/si';
import { RxSpeakerLoud, RxSpeakerOff } from 'react-icons/rx';
import { ref, set, getDatabase } from "firebase/database";
import app from '../../../../firebase/firebase';
import { useMediaQuery } from 'react-responsive';

import FlashcardConfetti from './FlashcardConfetti';

// import TinderCard from 'react-tinder-card';

import './Flashcard.css';

const Flashcard = (props) => {

    // const ref = useRef([])
    // const setName = props.name;
    const database = getDatabase(app);
    const currentDate = new Date();
    const navigate = useNavigate();
    // setData should be an Object
    const setData = props.cards;
    const setPK = props.PK;
    const setCompletions = props.completions;

    // setup responsive design
    const isMobile = useMediaQuery({ query: '(max-width: 950px)' });
    const isLandscape = useMediaQuery({ query: '(orientation: landscape)' });

    // signature
    const [signatureModal, setSignatureModal] = useState(false);
    const [signature, setSignature] = useState("");
    const [modalWarning, setModalWarning] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    // audio
    const [audioFile, setAudioFile] = useState(''); 
    const [isAudio, setIsAudio] = useState(false);

    // const [mouseInfo, setMouseInfo] = useState({ activeTimer: false, timeOver: false, message: "" });
    const [cards, setCards] = useState([]);
    const [toggles, setToggles] = useState([]);
    const [currentIdx, setCurrentIdx] = useState(0);
    const [isRemoved, setIsRemoved] = useState([]);

    const [hasBeenFlipped, setHasBeenFlipped] = useState(false); 
    const [crctCardCount, setCrctCardCount] = useState(0);
    const [inctCardCount, setInctCardCount] = useState(0);

    useEffect(()=>{
        let cardsArray = [];
        let isReversedArray = [];
        let counter = 0;
        let toggleState = [];
        for (let i in setData) {
            let tState = {
                id: counter,
                ref: createRef(),
                isFlipped: false,
                hasBeenFlipped: false
            }
            toggleState = toggleState.concat(tState);
            isReversedArray = isReversedArray.concat(false);
            cardsArray.push(setData[i])
            counter = counter + 1;
        }

        // console.log(toggleState);
        setToggles(toggleState);
        // console.dir(cardsArray);
        setCards(cardsArray);

        // set the audio file (if we aren't finished!)
        console.log("Card Count: " + Object.keys(cardsArray).length)
        if (currentIdx < Object.keys(cardsArray).length) {
            const currentWord = cardsArray[currentIdx].Front;
            try{
                const currentFile = require(`../../../../assets/audio/${currentWord}.mp3`);
                setAudioFile(currentFile);
                setIsAudio(true);
            }
            catch(e) {
                setAudioFile(null);
                setIsAudio(false);
            }
        }
        else {
            console.log("UPDATE THE TIMESTAMPS!!")


        }
        // else: we've finished! play the confetti!


    }, [setCards, setData, setHasBeenFlipped, currentIdx])

    const totalCards = cards.length;
    const percentComplete = currentIdx / totalCards;

    const handleFlip = (idx) => {
        
        toggles[idx].ref.current.toggle();
        setHasBeenFlipped(true);

        let newToggles = toggles;  
        newToggles[idx].hasBeenFlipped = true;
        setToggles(newToggles);      
    }

    const handleInct = (idx) => {
        let updateIsRemoved = isRemoved;
        updateIsRemoved = updateIsRemoved.concat(idx);
        let updateCurrentIdx = currentIdx + 1;

        setHasBeenFlipped(false);
        setIsRemoved(updateIsRemoved);
        setCurrentIdx(updateCurrentIdx);
        setInctCardCount(inctCardCount+1);
    }

    const handleCrct = () => {
        let updateIsRemoved = isRemoved.concat(currentIdx);
        let updateCurrentIdx = currentIdx + 1;
        
        setHasBeenFlipped(false);
        setIsRemoved(updateIsRemoved);
        setCurrentIdx(updateCurrentIdx);
        setCrctCardCount(crctCardCount+1);
    }

    const cardStyle = (idx) => { 
        return(
            {
                margin: "25px 5px", 
                width: "500px",
                height: "300px",
                position: "absolute",
                top: "-25px",
                left: "-255px",

                display: "flex",
                flexDirection: "column",
                justifyContent: "center",
                alignItems: "center",

                zIndex: totalCards-idx
            }
        )
    }   

    const fullBarStyle = () => {
        if (isMobile && isLandscape) {
            return(
                {
                    height: "20px",
                    width: "100%",
                    backgroundColor: "#e0e0de",
                    borderRadius: "50",
                    margin: "50",
                    border: "1px solid black"
                }
            )
        }
        else {
            return(
                {
                    height: "25px",
                    width: "100%",
                    backgroundColor: "#e0e0de",
                    borderRadius: "50",
                    margin: "50",
                    border: "1px solid black"
                }
            )
        }
    }

    const percentBarStyle = () => {
        if (isMobile && isLandscape) {
            return(
                {
                    height: "100%",
                    width: `${percentComplete*100}%`,
                    backgroundColor: "#A3E4D7",
                    borderRadius: 'inherit',
                    // textAlign: 'center',
                    fontSize: "0.75em",
                    zIndex: "0",
                    transition: 'width 1s ease-in-out',
                }
            )
        }
        else {
            return(
                {
                    height: "100%",
                    width: `${percentComplete*100}%`,
                    backgroundColor: "#A3E4D7",
                    borderRadius: 'inherit',
                    // textAlign: 'center',
                    fontSize: "1em",
                    zIndex: "0",
                    transition: 'width 1s ease-in-out',
                }
            )
        }
    }

    //not currently using
    // const toggleCard =() => {
    //     console.log("Clicked!")
    //     setIsFront(!isFront);
    //     document.getElementById("Card").classList.toggle("flipCard");
    // }

    // this function is executed when setTimeout reaches the allotted time (500ms)
    // const overTime = () => {
    //     setMouseInfo({
    //         activeTimer: false,
    //         timeOver: true,
    //         message: "More than 500ms have passed!"
    //     });
    // };

    // this function is what initially happens when the mouse is pressed
    // const handleOnMouseDown = () => {
    //     const timeoutID = setTimeout(overTime, 500);
    //     setMouseInfo({
    //         activeTimer: timeoutID,
    //         timeOver: false,
    //         message: "Mouse is being pressed..."
    //     });
    // };

    // this function is what happens when the mouse is released
    // const handleOnMouseUp = () => {
    //     // if there is still an active timer i.e. timer hasn't triggered, then...
    //     if (mouseInfo.activeTimer) {
    //         clearTimeout(mouseInfo.activeTimer);
    //         setMouseInfo({
    //             activeTimer: false,
    //             timeOver: false,
    //             message: "Mouseclick LESS THAN 500ms"
    //         });
    //     // if the active timer has expired and been reset, then acknowledge that the time has been surpassed
    //     } else {
    //         setMouseInfo({
    //             activeTimer: false,
    //             timeOver: true,
    //             message: "Mouseclick MORE THAN 500ms"
    //         });
    //     }
    // };

    const playAudio = (event) => {
        event.stopPropagation();
        let currentAudio = new Audio(audioFile);
        var playPromise = currentAudio.play();
            // In browsers that don’t yet support this functionality, playPromise won’t be defined.
            if (playPromise !== undefined) {
                playPromise.then(function() {
                    //automatic playback started
                }).catch(function(error) {
                    console.log(error);
                    console.log("try to reload");
                });
            }
    }

    const handleNoAudio = (event) => {
        event.stopPropagation();
        console.log("Do something special?")
    };

    const handleBackFontSize = (s) => {
        if (s.length >= 80) {
            return({
                fontSize: "1.20em",
                // color: "red"
            })
        }
        else if (s.length >= 55 && s.length < 80) {
            return({
                fontSize: "1.5em",
                // color: "pink"
            })
        }   
        else if (s.length >= 30 && s.length < 55) {
            return({
                fontSize: "1.75em",
                // color: "orange"
            })
        }   
        else {
            return({
                fontSize: "2em",
                // color: "green"
            });
        }
    };

    const handleSignatureModalClose = () => {
        setSignatureModal(false);
    };

    const handleSignatureModalOpen = () => {
        setSignatureModal(true);
    };

    const handleSignature = (event) => {
        setSignature(event.target.value)
    };

    const handleFinalSignature = () => {
        if (signature.length > 0) {
            // this will be the final object to submit to Firebase
            setIsLoading(true);
            var newCompletions = setCompletions;
            var newTimeStamp =    
                currentDate.getFullYear() + '-' +
                currentDate.getMonth()    + 1 + '-' + 
                currentDate.getDay()      + '-' + 
                currentDate.getHours()    + '-' + 
                currentDate.getMinutes()  + '-' + 
                currentDate.getSeconds();
            
            // newTimeStamp is the key, signature is the value
            newCompletions[newTimeStamp] = {
                user: signature,
                crct: crctCardCount,
                inct: inctCardCount,
            };
            console.log(newCompletions);

            // send data to Firebase
            const refSection = ref(database, ('Flashcards/' + setPK + '/SetCompletions'));
            set(refSection, newCompletions)
            .then(()=>{
                setIsLoading(false);
                // setIsUploaded(true);
                navigate("/FlashcardLibrary");
            });
        }
        else {
            setModalWarning(true);
        }
    }

    return(
        <div className="FlashcardContainer">
        
            <div className="ProgressBarContainer">
                <div className="ProgressVisual">
                    <div className="Outer" style={fullBarStyle()} >
                        <div className="Inner" style={percentBarStyle()} >
                            <span>{Math.ceil(percentComplete*100)}%</span>
                        </div>
                    </div>
                </div>
            </div>

            <div className="ScoreboardContainer">
                <div className="ScoreboardText">
                    Correct
                    <div className="CorrectScore">
                        {crctCardCount}
                    </div>
                </div>
                <div className="ScoreboardText">
                    Incorrect
                    <div className="IncorrectScore">
                        {inctCardCount}
                    </div>
                </div>
                <div className="ScoreboardText">
                    Total
                    <div className="TotalScore">
                        {crctCardCount + inctCardCount}
                    </div>
                </div>
            </div>

            {/* <div><Button onClick={()=>{setCurrentIdx(totalCards)}}>Push</Button></div> */}
        
            {cards.map((card, idx)=> 
                {
                    if (isRemoved.includes(idx)) {
                        return(null);
                    }
                    else if (currentIdx===idx) {
                        return(
                            <div key={idx} style={{ position: "relative", display: "flex", flexDirection: "row", justifyContent: "center", alignItems: "center" }}>
                                {isMobile && isLandscape ?
                                    hasBeenFlipped && <Button variant="outline-danger" style={{ width: "100px", position: "absolute", right: "285px", top: "100px" }} onClick={()=>handleInct(currentIdx)}>Incorrect</Button>
                                :
                                    null
                                }
                                <Flippy 
                                    flipOnHover={false} 
                                    flipOnClick={false}
                                    isFlipped={toggles[idx].isFlipped}
                                    flipDirection="horizontal"
                                    onClick={()=>handleFlip(idx)}
                                    ref={toggles[idx].ref}
                                    style={cardStyle(idx)}
                                >
                                    <FrontSide className="FCardFront" id="FrontDiv">
                                        <div className="GridFiller"></div>
                                        <div className="GridFiller"></div>
                                        <div className="GridMain">{card.Front}</div>
                                        <div className="GridFiller"></div>
                                        <div className="GridFiller"></div>
                                    </FrontSide>
                                    <BackSide className="FCardBack">
                                        <div className="GridTop">
                                            <div className="GridTopWords">
                                                <div className="GridOriginalWord"><b>{card.Front}</b></div>
                                                <div className="GridWordType"><i>{card.WordType}</i></div>
                                            </div>
                                            <div className="GridTopAudio"> 
                                                {isAudio ? 
                                                    <div className="AudioButton" id="audiobutton" onClick={(event)=>playAudio(event)}>
                                                        <RxSpeakerLoud id="audiobutton" />
                                                    </div>
                                                :
                                                    <div className="AudioButton" id="audiobutton" onClick={(event)=>handleNoAudio(event)}>
                                                        <RxSpeakerOff id="audiobutton" />
                                                    </div>
                                                }
                                                
                                            </div>
                                        </div>
                                        <div className="GridMiddle" id="BackDivNonAudio" style={handleBackFontSize(card.Back)}>
                                            {card.Back}
                                        </div>
                                        <div className="GridBottom" id="BackDivNonAudio"><i>Ex: {card.Example}</i></div>
                                    </BackSide>
                                </Flippy>
                                {isMobile && isLandscape ?
                                    hasBeenFlipped && <Button variant="outline-success" style={{ width: "100px", position: "absolute", left: "285px", top: "100px" }} onClick={()=>handleCrct(currentIdx)}>Correct</Button>
                                :
                                    null
                                }
                            </div>
                        )
                    }
                    else {
                        return(null);
                    }
                }
            )}

            {isMobile && isLandscape ?
                <div style={{ width: "15px", height: "315px" }}> 
                {totalCards-currentIdx===0 
                        ?
                        <>
                            <Button variant="outline-secondary" 
                                    size="lg" 
                                    style={{ margin: "100px 25px 0px -30px"}}
                                    onClick={handleSignatureModalOpen}>
                                Sign
                            </Button>
                            <FlashcardConfetti />
                        </>
                        :
                        null
                    }
                </div>
            :
                <div className="ButtonsContainer">
                    {hasBeenFlipped && <Button variant="outline-danger" style={{ width: "100px" }} onClick={()=>handleInct(currentIdx)}>Incorrect</Button>}
                    {totalCards-currentIdx===0 
                        ?
                        <>
                            <Button variant="outline-secondary" size="lg" onClick={handleSignatureModalOpen}>Sign</Button>
                            <FlashcardConfetti />
                        </>
                        :
                        <Button variant="outline-secondary" style={{ margin: "0px 25px"}}  onClick={()=>handleFlip(currentIdx)}>
                            Flip
                        </Button>
                    }
                    {hasBeenFlipped && <Button variant="outline-success" style={{ width: "100px" }} onClick={()=>handleCrct(currentIdx)}>Correct</Button>}
                </div>
            }
            





            <>
                <Modal
                    show={signatureModal}
                    onHide={handleSignatureModalClose}
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Sign off on your study</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div style={{marginBottom: "10px"}}>Sign your name to certify completion:</div>
                        <InputGroup className="mb-3">
                            <Form.Control
                            placeholder="Your Name"
                            onChange={handleSignature}
                            />
                        </InputGroup>
                    </Modal.Body>
                    <Modal.Footer>
                        <div className="ModalFooter">

                            <div className="ModalSpinner">
                                <div className="spinner">
                                    {isLoading 
                                        ? (
                                        <div className="ModalLoader">
                                            <div className="ModalInner one"></div>
                                            <div className="ModalInner two"></div>
                                            <div className="ModalInner three"></div>
                                        </div>
                                        ) 
                                        :   null 
                                    }
                                </div>
                            </div>

                            <div className="NoTextWarning">
                                {modalWarning ?
                                    <span><b>Please enter your name</b></span>
                                :
                                    <span>{null}</span>
                                }
                            </div>

                            <Button variant="primary" onClick={handleFinalSignature}>Sign</Button>
                        </div>
                    </Modal.Footer>
                </Modal>
            </>





           {/* The following code was testing for mouse-click lengths. Maybe necessary for using TinderCard (flip vs swipe) */}
            {/* <div className="MouseHoldTest">
                <div className="MousePressTest" onMouseDown={handleOnMouseDown} onMouseUp={handleOnMouseUp}>
                    Hold the Mouse Down!
                </div>
                <p>{mouseInfo.message}</p>
            </div> */}

            {/* The following code was made with TinderCard. Revive, if necessary, for Stage 2+ */}
            {/* <div className="Card" id="Card" key={1}>
                <div className="CardFront" id="CardF" onClick={toggleCard}>
                    <h2>FRONT</h2>
                </div>
                <div className="CardBack">
                    <TinderCard className="pressable" preventSwipe={"['up', 'down']"} onClick={toggleCard}>
                        <div className="TCardBack" onClick={toggleCard}>
                            <h2>BACK</h2>
                        </div>
                    </TinderCard>
                </div>
            </div> */}
        </div>
    )
}

export default Flashcard;