import React, { useRef } from 'react';

import './RefIntro.css';

const RefIntro = () => {

    const divRef = useRef(null);
    const inputRef = useRef(null);
    const buttonRef = useRef(null);

    // const [tempState, setTempState] = useState(0);


    // const tempFunction = () => {
       
    // };

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Basic Ref Functionality</h2>
            </div>
            <br />
            <div className="EntryPurpose">
                <b>Purpose: </b>
                <span>To gain a deeper understand of Refs and their usage.</span>
            </div>
            <br />
            <div className="EntryReadme">
                <b>Use: </b>
                <span>The goal of this entry is to tap into DOM elements and</span>
                <span>either modify their behavior or use their data using Refs.</span>
            </div>
            
            <div className="Buttz" ref={divRef}>
                <div className="EntryInput">
                    <input ref={inputRef} />  
                </div>
                <div className="EntryButton">
                    <button ref={buttonRef}>Push</button>
                </div>
            </div>
        </div>
    )
};

export default RefIntro;