    const [guessInput, setGuessInput] = useState("");
    const [guessedLetters, setGuessedLetters] = useState([]);
    const [inctLetters, setInctLetters] = useState([]);
    const [messageState, setMessageState] = useState('none');
    const [winState, setWinState] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [modalInput, setModalInput] = useState(["","","",""]);
    const [modalFocus, setModalFocus] = useState(0);
    const [modalIsLoading, setModalIsLoading] = useState(false);

    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });
    const isLandscape = useMediaQuery({ query: '(orientation: landscape)' });

    useEffect(() => {

        if (showModal) {
            setTimeout(() => {
                document.getElementById("initials" + modalFocus).focus()
                console.log("useEffect: Focus on #" + modalFocus );
              }, .01)
        } else {
            inputRef.current.focus();
            inputRef.current.click();
        }


        const fetchData = async () => {
            console.log("I've hit the DB " + updateFetchCount + " times.")
            const dbRef = ref(getDatabase());
            await get(child(dbRef, 'Games/Guessem/'))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    let queryData = snapshot.val();
                    //may not need to save this whole object to state
                    setGameData(queryData);
                    
                    let defaultSetName = queryData.default;
                    let defaultSetWords= queryData.Sets[defaultSetName];
                    console.log("defaultSet / WordObj");
                    console.log(defaultSetWords.length);
                    
                    setWordObj(defaultSetWords);
                    
                    // create the word list
                    let tempWordList = [];
                    for (let i=0; i<Object.keys(defaultSetWords).length; i++) {
                        console.log(defaultSetWords[i])
                        tempWordList = tempWordList.concat(defaultSetWords[i].word);
                    }

                    setWordList(tempWordList);

                } else {
                    console.log("No Data Available")
                }
            })
            .catch((error) => {
                console.error(error);
            })
        }
        
        if (fetchCount > 0) {
            //do nothing
        } else {
            console.log("DB hit!")
            fetchData();
        }

        let updateFetchCount = fetchCount + 1;
        setFetchCount(updateFetchCount);
        // console.log("FetchCount: " + updateFetchCount)

}, [guessedLetters, showModal, modalFocus, modalInput]);
                                                    // why is guessInput here???
