    //     },
    // };

    // const tempWordObj = {
    //     1: {
    //         wordType: 'verb',
    //         japanese: '怖がれる',
    //         english: 'to scare someone or something',
    //         exampleSentence: 'Halloween is a great time to try to frighten all of the neighbors in my neighborhood.'
    //     },
    //     2: {
    //         wordType: 'adjective',
    //         japanese: '明るい',
    //         english: 'to be light or full of light (especially in color)',
    //         exampleSentence: 'The bright light entered the house through the cracks in the curtains.'
    //     },
    //     3: {
    //         wordType: 'adjective',
    //         japanese: '贅沢な',
    //         english: 'something that is very, very comfortable, pleasant, and usually expensive',
    //         exampleSentence: 'When Arisa travels, she only stays at luxurious hotels with fancy rooms and wonderful service.'
    //     },
    //     4: {
    //         wordType: 'verb',
