    //     },
    //     9: {
    //         wordType: 'noun',
    //         japanese: 'カップル・二人・二つ・ペア',
    //         english: 'two people or things, usually considered a set',
    //         exampleSentence: 'The couple bought a couple of matching rings.'
    //     },
    //     10: {
