import React from 'react';

import './DivButtonTest.css';

const DivButtonTest = () => {

    // const [buttonClicked, setButtonClicked] = useState(0);
    // const [divClicked, setDivClicked] = useState(0);

    // const handleButtonClick = (event) => {
    //     console.log(event)
    //     setButtonClicked(counter => counter + 1);
    // };

    const handleMasterDivClick = (event) => {
        console.log("Hello from Master.");
    };

    const handleSubDivClick = () => {
        console.log("Hello from SubDiv.");
    };

    const handleChildOneClick = () => {
        console.log("Hello from ChildOne.");
    };

    const handleChildTwoClick = (event) => {
        event.stopPropagation();
        console.log("Hello from ChildTwo.");
    };

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Event Delegation and Propagation</h2>
            </div>
            <br />
            <div className="EntryPurpose">
                <b>Purpose: </b>
                <span>To discover the level of interconnectedness between elements in a div.</span>
                <span>If a button and a div both have onClick handlers, how can we specifically manage both?</span>
            </div>
            <br />
            <div className="EntryReadme">
                <b>Use: </b>
                <span>The following has 3 specific divs: 'master', 'sub', and 'child'.</span>
                <span>Click the buttons for different messages in console log.</span>
                <span>Compare children to see how event delegation is handled differently. </span>
            </div>
            <div className="TestDiv" onClick={handleMasterDivClick}>
                <div>There is some content in here</div>
                <div>There is some more content here</div>
                <div className="SubDivContainer" onClick={handleSubDivClick}>
                    <div className="ChildOne" onClick={handleChildOneClick}>One</div>
                    <div className="ChildTwo" onClick={handleChildTwoClick}>Two</div>
                </div>
            </div>
        </div>
    )
};

export default DivButtonTest;