import React from 'react';

import RefIntro from './RefIntro';
import DBArray from './DBArray';
import DynamicToggles from './DynamicToggles';
import TimeStamp from './TimeStamp';
import DivButtonTest from './DivButtonTest';
import MediaQueries from './MediaQueries';


import './Notebook.css';

const Notebook = () => {
    return(
        <div>
            <div className="NotebookTitle">
                Notebook
            </div>
            <hr/>

            <div className="NotebookEntry">
                <MediaQueries />
            </div>
            <hr />

            {/* 2023-10-26 */}
            <div className="NotebookEntry">
                <RefIntro />
            </div>
            <hr />

            {/* 2023-10-03 */}
            <div className="NotebookEntry">
                <DBArray />
            </div>
            <hr />

            <div className="NotebookEntry">
                <DynamicToggles />
            </div>
            <hr />

            <div className="NotebookEntry">
                <TimeStamp />
            </div>
            <hr />

            <div className="NotebookEntry">
                <DivButtonTest />
            </div>
            
            
        </div>
    )
}

export default Notebook;