import React from 'react';
import { Card } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

import GuessemLogo from '../../assets/game images/GuessemLogo_NoBorder.png';

import './GameLibrary.css';

const GameLibrary = () => {
    
    const navigate = useNavigate();

    const games = ['Guessem: Secret Spelling Words'];

    const handleClick = () => {
        navigate('/Guessem');
    };

    return(
        <div className="GameLibraryContainer">
            <div className="SectionTitle">
                Game Library
            </div>
            <div className="GameCardContainer">
                {games.map((game, idx) => (
                    <Card key={idx} style={{ width: "20rem", border: "3px solid black" }} onClick={handleClick}>
                        <Card.Img variant="top" src={GuessemLogo} style={{ backgroundColor: "lightgray", borderBottom: "2px solid black" }}/>
                        <Card.Body style={{ fontSize: "1.25em" }}>
                            {game} 
                        </Card.Body> 
                    </Card>
                    // <div key={idx} className="GameCard">
                    //     <div className="GameCardTopSection">asdf</div>
                    //     <div className="GameCardMiddleLine"></div>
                    //     <div className="GameCardBottomSection">{game}</div>
                    // </div>
                ))}
            </div>
        </div>
    )
};

export default GameLibrary;