import React, { useState, useEffect } from 'react';
import { Button, Accordion } from 'react-bootstrap';
import { getDatabase, ref, get, set, child } from "firebase/database";

import { Alert, AlertTitle } from '@mui/material';
import { Dialog, DialogActions, DialogContent, DialogTitle } from '@mui/material';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { FormControl, FormLabel, FormControlLabel, FormHelperText } from '@mui/material';
import { RadioGroup, Radio } from '@mui/material';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';

import { BsCheck2Square } from 'react-icons/bs';
import { TbTrash } from "react-icons/tb";
import { BsX } from "react-icons/bs";

// import Media from 'react-media';
import { useMediaQuery } from 'react-responsive';

import Loader from '../../Loader/Loader';

import './TeacherGames.css';

const TeacherGames = (props) => {

    const db = getDatabase();

    // the follownig TEMP variable is just for testing purposes. delete later
    // const [dbTitlesTEMP, setDBTitlesTEMP] = useState(['Title1: Original Title','Title2: Revenge of the Title','Title3: This Should be Kind of Long','Short Title','Title 5','Title 6','This is Title #7','Title Eight: This is the Longest Title in the Series, Just See What Happens']);
    const [dbTitles, setDBTitles] = useState([]);
    const [selectedDefault, setSelectedDefault] = useState('');
    const [errorDefault, setErrorDefault] = useState(false);
    const [helperTextDefault, setHelperTextDefault] = useState('');
    const [title, setTitle] = useState('');
    const [words, setWords] = useState(['']);
    const [wordTypes, setWordTypes] = useState(['']);
    const [japaneseDefs, setJapaneseDefs] = useState(['']);
    const [englishDefs, setEnglishDefs] = useState(['']);
    const [exSentences, setExSentences] = useState(['']);

    const [failDialogOpen, setFailDialogOpen] = useState(false);
    const [successDialogOpen, setSuccessDialogOpen] = useState(false);
    const [isLoading, setIsLoading] = useState(false);

    const [updateDomCount, setUpdateDomCount] = useState(0)
    
    useEffect(()=>{
        
        const fetchData = async () => {

            const dbRef = ref(getDatabase());
            await get(child(dbRef, 'Games/Guessem/Sets'))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    let queryData = snapshot.val();
                    setDBTitles(Object.keys(queryData));
                    
                } else {
                    console.log("No Data Available")
                }
            })
            .catch((error) => {
                console.error(error);
            })
            }
            fetchData();
    }, [db, updateDomCount])


    const handleRadioChange = (e) => {
        let updateSelectedDefault = e.target.value;
        setHelperTextDefault('');
        setSelectedDefault(updateSelectedDefault);
        setErrorDefault(false);
    };

    const handleTitleInput = (e) => {
        let updateTitle = e.target.value;
        setTitle(updateTitle);
    };


    const handleInput = (e, idx, type) => {
        let userInput = e.target.value;
        switch (type){
            case 'word':
                console.log("This input is: WORD");
                let updateWords = [...words];
                updateWords[idx] = userInput;
                setWords(updateWords);
                break;
            case 'type':
                console.log("This input is: TYPE");   
                let updateWordTypes = [...wordTypes];
                updateWordTypes[idx] = userInput;
                setWordTypes(updateWordTypes);        
                break;
            case 'japanese':
                console.log("This input is: JAPANESE");
                let updateJapaneseDefs = [...japaneseDefs];
                updateJapaneseDefs[idx] = userInput;
                setJapaneseDefs(updateJapaneseDefs);
                break;
            case 'english':
                console.log("This input is: ENGLISH");
                let updateEnglishDefs = [...englishDefs];
                updateEnglishDefs[idx] = userInput;
                setEnglishDefs(updateEnglishDefs);
                break;
            case 'example':
                console.log("This input is: EXAMPLE");
                let updateExSentences = [...exSentences];
                updateExSentences[idx] = userInput;
                setExSentences(updateExSentences);
                break;
            default: 
                console.log("Ya gone done fucked up")
        }
    };

    const handleAddClick = () => {
        let updateWords = [...words];
        let updateWordTypes = [...wordTypes];
        let updateJapaneseDefs = [...japaneseDefs];
        let updateEnglishDefs = [...englishDefs];
        let updateExSentences = [...exSentences];
        updateWords = updateWords.concat('');
        updateWordTypes = updateWordTypes.concat('');
        updateJapaneseDefs = updateJapaneseDefs.concat('');
        updateEnglishDefs = updateEnglishDefs.concat('');
        updateExSentences = updateExSentences.concat('');
        setWords(updateWords);
        setWordTypes(updateWordTypes);
        setJapaneseDefs(updateJapaneseDefs);
        setEnglishDefs(updateEnglishDefs);
        setExSentences(updateExSentences);
    };

    const handleDeleteEntry = (idx) => {
        let updateWords = [...words];
        let updateWordTypes = [...wordTypes];
        let updateJapaneseDefs = [...japaneseDefs];
        let updateEnglishDefs = [...englishDefs];
        let updateExSentences = [...exSentences];
        updateWords.splice(idx,1);
        updateWordTypes.splice(idx,1);
        updateJapaneseDefs.splice(idx,1);
        updateEnglishDefs.splice(idx,1);
        updateExSentences.splice(idx,1);
        setWords(updateWords);
        setWordTypes(updateWordTypes);
        setJapaneseDefs(updateJapaneseDefs);
        setEnglishDefs(updateEnglishDefs);
        setExSentences(updateExSentences);
        console.log("UpdatedWords: " + updateWords)
    };

    const handleDefaultSetTitle = (strName, strDev) => {
        switch (strDev){
            case 'mobile':
                if (strName.length > 46) {
                    console.log('Mobile + Filter')
                    return((strName.substring(0,43) + '...'));
                } else {
                    console.log('Mobile + No Filter')
                    return(strName);
                }
                // break;
            case 'pc':
                if (strName.length > 51) {
                    console.log("PC + Filter")
                    return((strName.substring(0,48) + '...'));
                } else {
                    console.log("PC + No Filter")
                    return(strName);
                }
                // break;
            default: 
                return("Ya gone done fucked up");
        }
    };

    //STYLES ----------

    const styleDefaultSelectError = {
        height: "15px", 
        margin: "10px 0px" 
    };

    const styleDefaultSelectSuccess = {
        height: "15px", 
        margin: "10px 0px",
        color: "#17B169"
    };

    const styleFormControlLabelMobile = {
        width: "265px", 
        height: "35px", 
        fontSize: '14px', 
        backgroundColor: "gainsboro", 
        display: "flex", 
        justifyContent: "center", 
        alignItems: "center", 
        marginRight: "0px",
    };

    const styleFormControlLabelPC = {
        width: "300px", 
        height: "45px", 
        fontSize: '14px', 
        // backgroundColor: "gainsboro", 
        display: "flex", 
        justifyContent: "left", 
        alignItems: "center", 
        marginRight: "0px",
    };

    const styleCreateSetTitleMobile = { 
        marginTop: "15px",
        marginBottom: "30px", 
        fontSize: "1.33em", 
        fontWeight: "600"
    };

    const styleCreateSetTitlePC = { 
        marginTop: "15px", 
        marginBottom: "30px" 
    };

    const styleTitleInputMobile = {
        width: "75vw", 
        margin: "0px",
    };

    const styleTitleInputPC = {
        width: "450px"
    };

    const styleTitleWarningMobile = {
        width: "75vw", 
        marginTop: "15px",
        marginBottom: "0px"
    };

    const styleTitleWarningPC = {
        padding: "3px", 
        paddingRight: "10px", 
        marginTop: "3px"
    };


    const styleEntryFieldsMobile = {
        width: "70vw",
    }

    const styleEntryFieldPC = {
        // width: "677px",
        width: "70vw",
    };


    //BOOLEAN OPERATIONS ----------

    const titleExists = () => {
        if (dbTitles.includes(title)) {
            return(true);
        } else {
            return(false);
        }
    };

    const isFinished = (idx) => {
        if (words[idx] && wordTypes[idx] && japaneseDefs[idx] && englishDefs[idx] && exSentences[idx]) {
            return(true);
        } else {
            return(false);
        }
    };

    const everyEntryFinished = () => {
        if (!titleExists() && title && words.every(e => e) && wordTypes.every(e => e) && japaneseDefs.every(e => e) && englishDefs.every(e => e) && exSentences.every(e => e)) {
            return true;
        } else {
            return false;
        }
    };

    const existingDBTitles = () => {
        if (dbTitles.length > 0) {
            return(true);
        } else {
            return(false);
        }
    };

    const isMobile = useMediaQuery({ query: '(max-width: 768px)' });
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' });

    const MobilePortrait = () => {
        if (isMobile && isPortrait) {
            return(true);
        } else {
            return(false);
        }
    };

    // SUBMIT FUNCTIONS ----------

    const submitUpdateDefault = (e) => {
        // stop the DOM from reloading
        e.preventDefault();

        console.log("Sending to the Database: " + selectedDefault);
        
        if (selectedDefault.length < 1) {
            setHelperTextDefault('Please select a default set before submitting');
            setErrorDefault(true);
        } else {
            // update helper message
            setHelperTextDefault('Default Word Set Updated!');
            setErrorDefault(false);
            // submit
            const refDefault = ref(db, ('Games/Guessem/default'));
            set(refDefault, selectedDefault);
        }
        
    };

    const submitFail = () => {
        setFailDialogOpen(true);
    };

    const handleFailDialogClose = () => {
        setFailDialogOpen(false);
    };

    const failDialogMsg = () => {
        if (!title) {
            return("No title entered. Please give a title to this word set.");
        } else if (title && titleExists()) {
            return("This title name already exists. Please choose a new name.");
        }
        else {
            return("");
        }
    };

    const submitSuccess = () => {
        setSuccessDialogOpen(true);
    };

    const handleSuccessDialogClose = () => {
        setSuccessDialogOpen(false);
    };



    const handleSuccessDialogSubmit = () => {
        setIsLoading(true);
        
        let wordSetObj = {};
        wordSetObj[title] = {};
        for (let i=0; i<words.length; i++) {
            wordSetObj[title][i] = {
                word: words[i],
                wordType: wordTypes[i], 
                japaneseDef: japaneseDefs[i], 
                englishDef: englishDefs[i], 
                exSentence: exSentences[i],
            }
        }
        console.log(wordSetObj);

        // submit
        const refSection = ref(db, ('Games/Guessem/Sets/' + title));
        set(refSection, wordSetObj[title]);

        // animation and navigate away
        setTimeout(function () {
            setSuccessDialogOpen(false);
            props.fSwitchTab('home');
            // reset all variables
            resetVars();
        }, 750);

        const resetVars = () => {
            let updateUpdateDomCount = updateDomCount;
            updateUpdateDomCount = updateUpdateDomCount + 1;
            setTitle('');
            setWords(['']);
            setWordTypes(['']);
            setJapaneseDefs(['']);
            setEnglishDefs(['']);
            setExSentences(['']);

            setFailDialogOpen(false);
            setSuccessDialogOpen(false);
            setIsLoading(false);

            setUpdateDomCount(updateUpdateDomCount);
        };
    };


    return(
        <div className="TeacherGamesContainer">
            <div className="TeacherGamesGuessemManagement">
                <div>
                    <Accordion style={{ width: "90vw" }}>
                        <Accordion.Item eventKey="0">
                            <Accordion.Header ><div className="AccordionHeader">Select a Default Set</div></Accordion.Header>
                            <Accordion.Body>
                                {existingDBTitles() ? 
                                    <div className="TeacherGamesGuessemManagementSelectDefaultSetContainer">
                                        <form onSubmit={submitUpdateDefault}>
                                            <FormControl className="GuessemFormControl" error={errorDefault}>
                                                <FormLabel id="GuessemManagementSelectDefaultFormLabel">
                                                    <div style={{ margin: "15px 0px" }}>
                                                        Select Default Word Set:
                                                    </div>
                                                </FormLabel>
                                                <RadioGroup name="TeacherGamesGuessemManagementSelectDefaultRadioButtonsGroup" onChange={handleRadioChange}>
                                                    <div className="TeacherGamesGusesemManagementSelectDefaultSetRadioGrid"> 
                                                        {dbTitles.map((iTitle, idx)=>(
                                                            MobilePortrait() ?  
                                                                <FormControlLabel key={idx} value={iTitle} control={<Radio />} label={handleDefaultSetTitle(iTitle, 'mobile')} 
                                                                                  sx={{ '& .MuiFormControlLabel-label': styleFormControlLabelMobile }}
                                                                />
                                                            : 
                                                                <FormControlLabel key={idx} value={iTitle} control={<Radio />} label={handleDefaultSetTitle(iTitle, 'pc')} 
                                                                                  sx={{ '& .MuiFormControlLabel-label': styleFormControlLabelPC }}
                                                                />
                                                        ))}
                                                    </div>
                                                </RadioGroup>
                                                <FormHelperText style={errorDefault ? styleDefaultSelectError : styleDefaultSelectSuccess}>
                                                    {helperTextDefault}
                                                </FormHelperText>
                                                <Button type="submit" variant="outline-primary" style={{ width: "250px" }}>
                                                    Update
                                                </Button>
                                            </FormControl>
                                        </form>
                                    </div>
                                 :
                                    <div>
                                        There are no Guessem sets in the database
                                    </div> 
                                }
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                            <Accordion.Header><div className="AccordionHeader">Create a New Set</div></Accordion.Header>
                            <Accordion.Body>
                                <div>
                                    <h2 style={MobilePortrait() ? styleCreateSetTitleMobile : styleCreateSetTitlePC }>Create a Word Set for Guessem</h2>
                                </div>
                                <div className="TeacherGamesGuessemManagementTitle">
                                    <span>
                                        <TextField value={title} id="outlined-basic" label="Title" variant="outlined" size="small" margin="dense" 
                                                   onChange={(e)=>handleTitleInput(e)} style={MobilePortrait() ? styleTitleInputMobile : styleTitleInputPC} />
                                    </span>
                                    {titleExists() ? 
                                        <span className="TeacherGamesGuessemManagementTitleWarnings">
                                            <Alert severity='error' sx={MobilePortrait() ? styleTitleWarningMobile : styleTitleWarningPC}>
                                                This title already exists
                                            </Alert>
                                        </span>
                                    : 
                                        null
                                    }
                                </div>
                                <div className="TeacherGamesGuessemManagementEntryContainer">
                                    {words.map((word, idx)=>(
                                        <div key={idx} className="TeacherGamesGuessemManagementWordEntryContainer">
                                            <div className="TeacherGamesGuessemManagementWordEntryHeader">
                                                <div className="TeacherGamesGuessemManagementWordEntryTitle"><h5 style={{ margin: "0px" }}>Word Entry #{idx+1}</h5></div>
                                                <div className="TeacherGamesGuessemManagementWordEntryDeleteButton" onClick={()=>handleDeleteEntry(idx)}>
                                                    <TbTrash className="trashIcon" />
                                                </div>
                                            </div>
                                            <div className="TeacherGamesGuessemManagementWordEntryTop">
                                                <span><TextField value={words[idx]} label="Word"                       variant="outlined" size="small" margin="dense" onChange={(e)=>handleInput(e,idx,'word')}     style={MobilePortrait() ? styleEntryFieldsMobile : styleEntryFieldPC}/></span>
                                                <span><TextField value={wordTypes[idx]} label="Word Type"              variant="outlined" size="small" margin="dense" onChange={(e)=>handleInput(e,idx,'type')}     style={MobilePortrait() ? styleEntryFieldsMobile : styleEntryFieldPC}/></span>
                                                <span><TextField value={japaneseDefs[idx]} label="Japanese Definition" variant="outlined" size="small" margin="dense" onChange={(e)=>handleInput(e,idx,'japanese')} style={MobilePortrait() ? styleEntryFieldsMobile : styleEntryFieldPC}/></span>
                                            </div>
                                            <div className="TeacherGamesGuessemManagementWordEntryMiddle">
                                                <span><TextField value={englishDefs[idx]} label="English Definition"   variant="outlined" size="small" margin="dense" onChange={(e)=>handleInput(e,idx,'english')}  style={MobilePortrait() ? styleEntryFieldsMobile : styleEntryFieldPC}/></span>
                                            </div>
                                            <div className="TeacherGamesGuessemManagementWordEntryBottom">
                                                <span><TextField value={exSentences[idx]} label="Example Sentence"     variant="outlined" size="small" margin="dense" onChange={(e)=>handleInput(e,idx,'example')}  style={MobilePortrait() ? styleEntryFieldsMobile : styleEntryFieldPC}/></span>
                                            </div>
                                            <div className="TeacherGamesGuessemManagementValidation">
                                                <div><BsCheck2Square className="TeacherGamesGuessemManagementCheck" style={isFinished(idx) ? {color: '#32CD32'} : {color: 'red'} }/></div>
                                                <div style={{ marginTop: "3.5px"}}>{isFinished(idx) ? "Entry is complete!" : "Please fill in each field"}</div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                                <div className="TeacherGamesGuessemManagementButtonsContainer">
                                        <Button className="TeacherGamesGuessemManagementAddButton" onClick={handleAddClick}>Add Word</Button>
                                        <Button className="TeacherGamesGuessemManagementCreateButton" variant={everyEntryFinished() ? 'success' : 'outline-danger'} onClick={everyEntryFinished() ? submitSuccess : submitFail}>Create Set</Button>
                                </div>
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </div>


                {/* FAIL DIALOG */}
                <div>
                    <Dialog open={failDialogOpen} onClose={handleFailDialogClose}>
                        <Alert severity='error' onClose={handleFailDialogClose} style={{ border: "2.5px solid #c62828", padding: "50px, 0px"}}>
                            <AlertTitle>
                                Unfinished Entry
                            </AlertTitle>
                            <span>Please fill in each field for every entry. You will see green check boxes when an entry is finished.</span>
                            <div><b>{failDialogMsg()}</b></div>
                        </Alert>
                    </Dialog>
                </div>

                {/* SUCCESS DIALOG */}
                <div>
                    <Dialog open={successDialogOpen} fullWidth={true} maxWidth='xl'  onClose={handleSuccessDialogClose}>
                        <DialogTitle id="alert-dialog-title">
                            {title}
                        </DialogTitle>
                        <IconButton onClick={handleSuccessDialogClose}
                            sx={{ position: 'absolute', right: 8, top: 8, color: (theme) => theme.palette.grey[500]}}>
                            <BsX />
                        </IconButton>
                        <DialogContent>
                            {/* <DialogContentText id="alert-dialog-description"> */}
                            <TableContainer component={Paper}>
                                <Table  aria-label="customized table">
                                    <TableHead style={{ margin: "0px", padding: "0px"}}>
                                    <TableRow style={{ background: "black" }}>
                                        {/* <TableCell align="right">Word Type</TableCell> */}
                                        <TableCell style={{ color: "white", fontSize: "1.15em" }}>Word</TableCell>
                                        <TableCell style={{ color: "white", fontSize: "1.15em" }}>Type</TableCell>
                                        <TableCell style={{ color: "white", fontSize: "1.15em" }}>Japanese</TableCell>
                                        <TableCell style={{ color: "white", fontSize: "1.15em" }}>English</TableCell>
                                        <TableCell style={{ color: "white", fontSize: "1.15em" }}>Example</TableCell>
                                    </TableRow>
                                    </TableHead>
                                    <TableBody>
                                    {words.map((word, idx) => (
                                        <TableRow key={idx}>
                                            <TableCell component="th" scope="row">{word}</TableCell>
                                            <TableCell>{wordTypes[idx]}</TableCell>
                                            <TableCell>{japaneseDefs[idx]}</TableCell>
                                            <TableCell>{englishDefs[idx]}</TableCell>
                                            <TableCell>{exSentences[idx]}</TableCell>
                                        </TableRow>
                                    ))}
                                    </TableBody>
                                </Table>
                                </TableContainer>
                            {/* </DialogContentText> */}
                        </DialogContent>
                        <DialogActions style={{marginBottom: "10px"}}>
                            {isLoading ? 
                                <div className="TeacherGamesGuessemManagementSuccessDialogLoader" style={{ marginRight: "25px" }}>
                                    <Loader />
                                </div>
                                :
                                null
                            }
                            <Button variant="outline-success" onClick={handleSuccessDialogSubmit} style={{ marginRight: "25px" }} autoFocus>
                                Submit
                            </Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </div>
        </div>
    )
};

export default TeacherGames;