import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import {getDatabase, get, ref, child } from "firebase/database";

import SetCard from "./SetCard";

import './FlashcardLibrary.css';


const FlashcardLibrary = () => {
    
    const [isLoading, setIsLoading] = useState(false);
    const [dbSets, setDbSets] = useState([]);
    const [stateData, setStateData] = useState(false);
    const [noData, setNoData] = useState(false);

    let location = useLocation();
    let db = getDatabase();
        

    useEffect(()=>{
        let flashcardSets = [];

        if (!location.state) {
            setStateData(false);
        }
        else {
            setStateData(true);
        }

        const fetchData = async () => {
            setIsLoading(true);

            const dbRef = ref(getDatabase());
            await get(child(dbRef, 'Flashcards/'))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    let queryData = snapshot.val();
                    for (let i in Object.keys(queryData)) {
                        flashcardSets = flashcardSets.concat(queryData[Object.keys(queryData)[i]]);
                        flashcardSets[i].PK = Object.keys(queryData)[i];
                    }

                    setDbSets(flashcardSets);
                    setIsLoading(false);
                    
                } else {
                    console.log("No Data Available")
                    setIsLoading(false);
                    setNoData(true);
                }
            })
            .catch((error) => {
                console.error(error);
            })
        }

        // Execute API call
        fetchData();
    }, [db, stateData, location.state]);
    
    return(
        <div className="FlashcardLibraryContainer">
            {/* Loader */}
            <div className="FlashcardLibraryLoader">
                {isLoading 
                    ? 
                        <div className="LibraryLoader">
                            <div className="LibraryInner one"></div>
                            <div className="LibraryInner two"></div>
                            <div className="LibraryInner three"></div>
                        </div>
                    : 
                        <div>
                            {noData 
                                ? 
                                    <div>There is no flashcard data in the database</div>
                                :             
                                    // start library
                                    <div className="FlashcardSetGrid">
                                        {dbSets.length>0 && dbSets.map((set, idx) => (
                                            // I need to fix this. setNum is a quickhack at replacing a PK. This is bad practice. Change ASAP
                                            <SetCard className="SetCard" key={idx} data={set} />
                                        ))}
                                    </div>
                            } 
                        </div>
                }
            </div>   

        </div>
    )
}

export default FlashcardLibrary