import React, { useState, useEffect } from 'react';
import { Button } from 'react-bootstrap';
import { BiRadioCircle } from 'react-icons/bi';
import { AiOutlineClose } from 'react-icons/ai';

import './DynamicToggles.css';


const DynamicToggles = () => {

    const [booleanListA, setBooleanListA] = useState([false]);
    const [booleanListB, setBooleanListB] = useState([false]);
    // const [booleanListC, setBooleanListC] = useState([false]);
    // const [booleanListD, setBooleanListD] = useState([false]);

    const [booleanObj, setBooleanObj] = useState({
        val1: false,
        val2: false
    });

    const [nComponents, setNComponents] = useState([1]);

    useEffect(()=> {
        // console.log("UseEffect is in effect");
    }, [booleanListA, nComponents])

    const addComponent = () => {
        let numComponents = nComponents.length
        let updateComponents = nComponents;
        updateComponents = updateComponents.concat(numComponents+1)
        // console.log("updateComponents: " + updateComponents)
        setNComponents(updateComponents);
        
        // must also update booleanList 
        let updateBooleanListA = [...booleanListA];
        updateBooleanListA = updateBooleanListA.concat(false);
        // console.log("updateBooleanList: " + updateBooleanListA)
        setBooleanListA(updateBooleanListA);
    };

    const removeComponent = () => {
        // console.log("removeComponent: ")
    };

    const addSubComponentA = (idx) => {
        let updateBooleanListA = [...booleanListA];
        updateBooleanListA[idx] = !updateBooleanListA[idx]
        setBooleanListA(updateBooleanListA);
    };

    const addSubComponentB = (idx) => {
        let updateBooleanListB = booleanListB;
        updateBooleanListB[idx] = !updateBooleanListB[idx]
        setBooleanListB(updateBooleanListB);
    };

    const addSubComponentC = (idx) => {
        let updateBooleanObj = {...booleanObj};
        updateBooleanObj.val1 = !updateBooleanObj.val1;
        setBooleanObj(updateBooleanObj);
    };

    const addSubComponentD = (idx) => {
        let updateBooleanObj = booleanObj;
        updateBooleanObj.val2 = !updateBooleanObj.val2;
        setBooleanObj(updateBooleanObj);
    };

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Dynamic Toggling</h2>
            </div>
            <br />
            <div className="EntryPurpose">
                <b>Purpose: </b>
                <span>To have an active reminder how to toggle multiple components without</span>
                <span>triggering all components.</span>
                <span>This misunderstanding has happened far too many times not to have an </span>
                <span>entry dedicated to it</span>
            </div>
            <br />
            <div className="EntryReadme">
                <b>Use: </b>
                <span>EXPLANATION</span>
                <span>MORE EXPLANATION</span>
            </div>
            
            <hr />

            <div className="EntryData">
                <div className="ButtonCockpit">
                    <Button variant="secondary" onClick={()=>addComponent()} style={{ margin: "0px 25px" }}>
                        Add Component
                    </Button>
                    <Button variant="secondary" onClick={()=>removeComponent()} style={{ margin: "0px 25px" }}>
                        Remove Component
                    </Button>
                </div>
                {nComponents.map((id, idx) => (
                    <div className="ComponentRow">
                        <div className="DynamicDataStyle">
                            <span>Component: {id}</span>
                            <span>Index: {idx}</span>
                        </div>
                        
                        {/* Working list variable column */}
                        <div className="SubComponentColumn">
                            <div>
                                <BiRadioCircle style={{ color: "limegreen", fontSize: "20px", marginBottom: "5px" }} /> List 
                            </div>
                            <div className="SubComponentButtons">
                                <Button variant="secondary" onClick={()=>addSubComponentA(idx)}>
                                    +
                                </Button>
                            </div>
                            <div className="SubComponentHidden">
                                {booleanListA[idx] ?
                                    <span>On</span>
                                    :
                                    <span>Off</span>
                                }
                            </div>
                        </div> 

                        {/* Non-working list variable column */}
                        <div className="SubComponentColumn">
                            <div>
                                <AiOutlineClose style={{ color: "red", fontSize: "20px", marginBottom: "5px" }} /> List
                            </div>
                            <div className="SubComponentButtons">
                                <Button variant="secondary" onClick={()=>addSubComponentB(idx)}>
                                    +
                                </Button>
                            </div>
                            <div className="SubComponentHidden">
                                {booleanListB[idx] ?
                                    <span>On</span>
                                    :
                                    <span>Off</span>
                                }
                            </div>
                        </div> 

                        {/* Working object variable column */}
                        <div className="SubComponentColumn">
                            <div>
                                <BiRadioCircle style={{ color: "limegreen", fontSize: "20px", marginBottom: "5px" }} /> Object
                            </div>
                            <div className="SubComponentButtons">
                                <Button variant="secondary" onClick={()=>addSubComponentC(idx)}>
                                    +
                                </Button>
                            </div>
                            <div className="SubComponentHidden">
                                {booleanObj.val1 ?
                                    <span>On</span>
                                    :
                                    <span>Off</span>
                                }
                            </div>
                        </div> 

                        {/* Non-working object variable column */}
                        <div className="SubComponentColumn">
                            <div>
                                <AiOutlineClose style={{ color: "red", fontSize: "20px", marginBottom: "5px" }} /> Object
                            </div>
                            <div className="SubComponentButtons">
                                <Button variant="secondary" onClick={()=>addSubComponentD(idx)}>
                                    +
                                </Button>
                            </div>
                            <div className="SubComponentHidden">
                                {booleanObj.val2 ?
                                    <span>On</span>
                                    :
                                    <span>Off</span>
                                }
                            </div>
                        </div> 
                    </div>
                ))}

            </div>
        </div>
    )

}

export default DynamicToggles;