import React, { useState, useEffect } from 'react';
import { Tab, Tabs } from 'react-bootstrap';
import { getAuth, onAuthStateChanged } from "firebase/auth";

import { getDatabase } from "firebase/database";

import { useMediaQuery } from 'react-responsive';

import './Teacher.css';

import TeacherTest from './TeacherTest/TeacherTest';
import TeacherBookReports from './TeacherBookReports/TeacherBookReports';
import TeacherHomework from './TeacherHomework/TeacherHomework';
import TeacherGames from './TeacherGames/TeacherGames';
import TeacherFlashcards from './TeacherFlashcards/TeacherFlashcards';

const Teacher = () => {

    let db = getDatabase();
    const auth = getAuth();

    const [userEmail, setUserEmail] = useState("");
    const [userStatus, setUserStatus] = useState(false);
    
    // 2023-11-25
    const [tabActiveKey, setTabActiveKey] = useState('home');

    useEffect(() => {
        onAuthStateChanged(auth, (user) => {
            if (user) {
                // User is signed in, see docs for a list of available properties
                // https://firebase.google.com/docs/reference/js/firebase.User
                //   const uid = user.uid;
                const updateUserEmail = user.email
                //   console.log(user)
              setUserEmail(updateUserEmail)
              setUserStatus(true);
            } else {
              // User is signed out
              // ...
              setUserEmail("You aren't allowed in here")
            }
          });
    }, [auth, db, tabActiveKey])

    const updateActiveKey = (str) => {
        console.log("Updating Stuff")
        let updateStr = (str)
        setTabActiveKey(updateStr);
    };

    const isMobile = useMediaQuery({ query: '(max-width: 768px)' });
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' });

    const MobilePortrait = () => {
        if (isMobile && isPortrait) {
            return(true);
        } else {
            return(false);
        }
    };

    const styleTabMobile = {
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
        fontSize: "0.575em",
    };

    const styleTabPC = {
        display:"flex", 
        flexDirection:"row", 
        justifyContent:"center", 
        fontSize: "1em"
    };

    return(
        <div className="TeacherContainer">
            {userStatus ? 
                <>
                <div className="SectionTitle">Teacher Console</div>
                <div className="TeacherNavigation">
                    <Tabs activeKey={tabActiveKey} onSelect={(k)=>setTabActiveKey(k)} id="teacher-navigation" className="mb-3" 
                    style={MobilePortrait() ? styleTabMobile : styleTabPC }>
                        <Tab eventKey="home" title="Home">
                            <div className="TeacherMessage">
                                <span><b>Teacher Home</b></span>
                                <br />
                                <br />
                                <span>Welcome back, {userEmail} </span>
                            </div>
                        </Tab>
                        <Tab eventKey="homework" title="Homework">
                            <TeacherHomework />
                        </Tab>
                        <Tab eventKey="games" title="Games">
                            <TeacherGames fSwitchTab={updateActiveKey} />
                        </Tab>
                        <Tab eventKey="flashcards" title="Flashcards">
                            <TeacherFlashcards />
                        </Tab>
                        <Tab eventKey="book-report" title="Book Reports">
                            <TeacherBookReports />
                        </Tab>
                        {/* <Tab eventKey="test" title="Test">
                            <TeacherTest />
                        </Tab> */}
                    </Tabs>
                </div>
                </>
            : 
                <div style={{ margin: "25px 0px", fontSize: "1.33em" }}> 
                    You are not authorized to use this page. Please sign in and try again. 
                </div> 
            
            }
        </div>
    ) 
};

export default Teacher;