import React from 'react';
import { useNavigate } from 'react-router-dom';

import './SetCard.css';

const SetCard = (props) => {
    
    const navigate = useNavigate();

    let setPK = props.data.PK;
    let setName = props.data.SetName;
    let setAuthor = props.data.SetAuthor;
    let setCards = props.data.SetCards;
    let setCompletions = props.data.SetCompletions;

    const handleClick = () => {
        console.log(props.data)
        navigate("/FlashcardStudy", { state: { setPK:setPK, setName:setName, setCards:setCards, setCompletions:setCompletions }});
    }

    return(
        <div className="SetCard" onClick={handleClick}>
            <div></div>
            <div className="SetName">
                <h4>{setName}</h4>
            </div>
            <div>
                {Object.keys(setCards).length} cards
            </div>
            <div className="SetAuthor">
                <div>Author:</div>
                <div>{setAuthor}</div>
            </div>
        </div>
    )
}

export default SetCard;