import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { Divide as Hamburger } from 'hamburger-react';
import { RiLogoutCircleLine, RiLoginCircleLine } from 'react-icons/ri';
import { PiChalkboardTeacher } from 'react-icons/pi';
import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";

import { SidebarData } from './SidebarData';

import './MobileHeader.css';

const MobileHeader = () => {

    const auth = getAuth();

    const [loggedIn, setLoggedIn] = useState(false);

    useEffect(() => {
        onAuthStateChanged(auth, (user) => {
            if (user) {
              // User is signed in, see docs for a list of available properties
              // https://firebase.google.com/docs/reference/js/firebase.User
            //   const uid = user.uid;
            //   const updateUserEmail = user.email
            //   console.log(user)
              setLoggedIn(true)
            } else {
              // User is signed out
              // ...
              setLoggedIn(false)
            }
          });
    }, [loggedIn, auth])

    const [isVisible, setIsVisible] = useState(false);

    const toggleIsVisible = () => {
        let updateIsVisible = !isVisible;
        setIsVisible(updateIsVisible);
    }

    const handleLogout = () => {
        signOut(auth).then(() => {
            console.log("Signed Out!")
          }).catch((error) => {
            console.log("Error!")
          });
    };

    return(
        <div className="MobileHeaderContainer">
            <div className="MobileHamburgerContainer">
                <Hamburger color="white" size={33} duration={1.25} distance="md" toggled={isVisible} toggle={toggleIsVisible} />
            </div>
            <div className={isVisible ? "MobileHeaderLinks active": "MobileHeaderLinks"}>
                <nav>
                    <ul onClick={toggleIsVisible}>
                        {SidebarData.map((item, idx) => (
                            <li key={idx} className={item.cName}>
                                <Link to={item.path}>
                                    <span style={{ marginRight: "9px" }}>{item.icon}</span>
                                    <span>{item.name}</span>
                                </Link>
                            </li>
                        ))}
                        
                        <li className="MobileLinkText">
                        {loggedIn ? <>
                                        <Link to="/Teacher">
                                            <span style={{ marginRight: "9px" }}><PiChalkboardTeacher /></span>
                                            <span>Teacher Home</span>
                                        </Link> 
                                    </>
                                    :   
                                    <>
                                        <Link to="/TeacherLogin">
                                            <span style={{ marginRight: "9px" }}><RiLoginCircleLine /></span>
                                            <span>Teacher Login</span>
                                        </Link>
                                    </>
                        }
                        </li>
                        {loggedIn ? <li className="MobileLinkText">
                                        <Link to="/Home" onClick={handleLogout}>
                                            <span style={{ marginRight: "9px" }}><RiLogoutCircleLine /></span>
                                            <span>Logout</span>
                                        </Link>
                                    </li> : null }

                    </ul>
                </nav>
            </div>
        </div>
    )
}

export default MobileHeader;