import React, { useState, useEffect } from 'react';
import { Dropdown, DropdownButton, InputGroup, FormControl, Button, Form } from 'react-bootstrap';
import { getDatabase, ref, set } from "firebase/database";
import { useNavigate } from 'react-router-dom';
import { BsCheck2Square } from 'react-icons/bs';
import './CreateHomeworkEntryForm.css';

const CreateHomeworkEntryForm = () => {

    const db = getDatabase();
    const navigate = useNavigate();

    const [selectedYear, setSelectedYear] = useState(null);
    const [selectedMonth, setSelectedMonth] = useState(null);
    const [selectedDay, setSelectedDay] = useState(null);

    const [prepDay, setPrepDay] = useState(false);

    const [xFields, setxFields] = useState([1]);

    const [homeworkItem, setHomeworkItem] = useState(['']);
    const [descriptionItem, setDescriptionItem] = useState(['']);
    const [toggleDesc, setToggleDesc] = useState([false]);

    const [isLoading, setIsLoading] = useState(false);

    useEffect(()=>{ 

    }, [toggleDesc])
    // const [zFields, setzFields] = useState([]);

    const yearList = ["2020","2021","2022", "2023"];
    const monthList= ["January","February","March","April","May","June","July","August","September","October","November","December"];
    // const thirtyOneDayList = ["January","March","May", "July","August","October","December"];

    const handleYearSelect = (e) => {
        // console.log(e)
        setSelectedYear(e)
        if (selectedMonth != null) {
            console.log("DO SHIT")
        }
      }

    const handleMonthSelect = (e) => {
        setSelectedMonth(e)
        if (selectedYear != null) {
            setPrepDay(true);
        }
    }

    const handleDaySelect = (e) => {
        setSelectedDay(e)
    }

    
    const daysInMonth = (month, year) => { // Use 1 for January, 2 for February, etc.
        return new Date(year, month, 0).getDate();
      }

    const generateDays = (selectedYear, selectedMonth) => {
        let DIY = daysInMonth(monthList.indexOf(selectedMonth)+1, selectedYear);
        let daysList = [];
        for (let i = 1; i<=DIY; i++) {
            daysList = daysList.concat(i);
            
        }
        return daysList;
    }

    const toggleDescription = (xID, xIdx) => {
        console.log("xID: " + xID);
        console.log("idx1: " + xIdx)
        let updateToggle = [...toggleDesc];
        updateToggle[xIdx] = !updateToggle[xIdx];
        console.log(updateToggle)
        setToggleDesc(updateToggle);
        
    };

    const addHomeworkItem = () => {
        let updateXFields = [...xFields];
        updateXFields = updateXFields.concat(updateXFields.length+1);
        setxFields(updateXFields);
    };

    const handleHomeworkItemInput = (idx, e) => {
        let updateHomeworkItem = [...homeworkItem];
        updateHomeworkItem[idx] = e;
        setHomeworkItem(updateHomeworkItem);
    };

    const handleDescriptionInput = (idx, e) => {
        let updateDescriptionItem = [...descriptionItem];
        updateDescriptionItem[idx] = e;
        setDescriptionItem(updateDescriptionItem);
    };

    const padZero = (n) => {
        if (n.toString().length > 1) {
            return(n.toString());
        }
        else {
            return("0" + n.toString());
        }
    };

    const submitHomeworkAssignment = () => {
        setIsLoading(true);
        
        // create JSON for submission
        let submissionField = {};
        submissionField[selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)] = {};

        console.log("LOG: " + descriptionItem)

        for (let i in xFields) {

            // check to see if the description field is empty. If so, set it to an empty string (don't just leave it as 'undefined')
            if (descriptionItem[i]) {
                submissionField[selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)]["EntryNumber"+i] = {
                    "HomeworkItem": homeworkItem[i],
                    "Description": descriptionItem[i]
                }
            }
            else {
                submissionField[selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)]["EntryNumber"+i] = {
                    "HomeworkItem": homeworkItem[i],
                    "Description": ""
                }
            

            // submissionField[selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)]["EntryNumber"+i] = {
            //     "HomeworkItem": homeworkItem[i],
            //     "Description": descriptionItem[i]
            }
        };
        console.log(submissionField);

        // submit
        const refSection = ref(db, ('HomeworkItems/'+ selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)));
        set(refSection, 
            submissionField[selectedYear + '-' + padZero(monthList.indexOf(selectedMonth)+1) + '-' + padZero(selectedDay)]);

        // animation and navigate away
        setTimeout(function () {
            setIsLoading(false);
            navigate("/HW");
        }, 750);
    };

    return(
        <div className="NewHomeworkEntryContainer">
            <div className="DateSelectionContainer">
                {/* <div className="BufferSpace">
                    
                </div> */}
                <div className="DateSelectionButtonsContainer">
                    <DropdownButton id="year" title="Year" onSelect={handleYearSelect}
                        style={{ marginTop: "15px", marginBottom: "15px", marginLeft: "10px", marginRight: "10px" }}>
                        {yearList.map((iYear, yearId) => (
                            <Dropdown.Item eventKey={iYear} key={yearId}>{iYear}</Dropdown.Item>
                        ))}
                    </DropdownButton>
                    <DropdownButton id="month" title="Month" onSelect={handleMonthSelect}
                        style={{ marginTop: "15px", marginBottom: "15px", marginLeft: "10px", marginRight: "10px" }}>
                        {monthList.map((iMonth, monthId) => (
                            <Dropdown.Item eventKey={iMonth} key={monthId}>{iMonth}</Dropdown.Item>
                        ))}
                    </DropdownButton>
                    <DropdownButton id="day" title="Day" onSelect={handleDaySelect}
                        style={{ marginTop: "15px", marginBottom: "15px", marginLeft: "10px", marginRight: "10px" }}>                
                        {!prepDay ? 
                            <Dropdown.Item eventKey="null">Select a day</Dropdown.Item>
                            :
                            generateDays(selectedYear, selectedMonth).map((day, idx) => (
                                <Dropdown.Item eventKey={day} key={idx}>{day}</Dropdown.Item>
                            ))
                        }
                    </DropdownButton>
                    
                </div>
                <div className="DateDisplayContainer">
                    <div className="DefaultText" >
                        Selected Date : 
                    </div>
                    <div className="DisplayedDate">
                        {selectedYear ? selectedYear : " "}    
                    </div>
                    <div className="DisplayedDate">
                        {selectedMonth ? selectedMonth : " "}    
                    </div>
                    <div className="DisplayedDate">
                        {selectedDay ? selectedDay : " "}    
                    </div>
                </div>
            </div>

            <div className="CreateHomeworkEntryFormContainer">
                <div className="AddHomeworkItemButton">
                    <Button variant="secondary" onClick={addHomeworkItem} >
                        Add Homework Item
                    </Button>
                </div>
                        
                {xFields.map((xID, xIdx) => (
                    <div key={xIdx} className="xEntryFields">
                        <div className="xRowFields">
                            <InputGroup className="mb-3" style={{ width: "75vw", marginBottom: "0px" }} >
                                <FormControl placeholder={"Homework Item " + xID} onChange={(e)=>handleHomeworkItemInput(xIdx, e.target.value)} style={{ width: "55%", margin: "0px" }}/>
                                <Button variant="secondary" onClick={()=>toggleDescription(xID,xIdx)}>Description +/-</Button>
                            </InputGroup>
                        </div>
                        <div className="DescriptionField">
                        {!toggleDesc[xIdx] ?
                            null                                               
                            :
                            <Form>
                                <Form.Group className="mb-3">
                                    <Form.Label>Homework Item Description</Form.Label>
                                    <Form.Control as="textarea" rows={2} onChange={(e)=>handleDescriptionInput(xIdx, e.target.value)} style={{ width: "725px" }} />
                                </Form.Group>
                            </Form>
                        }
                        </div>
                    </div>
                ))}

                <div className="LoaderSubmitButtonContainer">
                    <div className="SubmitLoader">
                        {isLoading ? (
                            <div className="loader">
                                <div className="inner one"></div>
                                <div className="inner two"></div>
                                <div className="inner three"></div>
                            </div>
                            ) 
                        : 
                        null
                        }
                    </div>
                    <div className="SubmitButton">
                        {((!(homeworkItem[0] === '')) && selectedYear && selectedMonth && selectedDay) ?
                            <Button type="submit" variant="success" onClick={submitHomeworkAssignment}>Post Homework</Button>                        
                        :
                            <Button type="submit" variant="outline-danger">Post Homework</Button>
                        }
                    </div>
                    <div className="DateCheck">
                        <span><BsCheck2Square style={selectedYear  ? { color: "green" } : { color: "red" } }/> Year</span>
                        <span><BsCheck2Square style={selectedMonth ? { color: "green" } : { color: "red" } }/> Month</span>                       
                        <span><BsCheck2Square style={selectedDay   ? { color: "green" } : { color: "red" } }/> Day</span>
                        
                    </div>
                </div>
                

            </div>
        </div>
    )
}

export default CreateHomeworkEntryForm;