import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import Flashcard from './Flashcard/Flashcard';
import { useMediaQuery } from 'react-responsive';

import './FlashcardStudySection.css';

const FlashcardStudySection = () => {
    
    const [isData, setIsData] = useState(false);
    const [setName, setSetName] = useState("");
    const [setData, setSetData] = useState([]);
    const [setPK, setSetPK] = useState("");
    const [setCompletions, setSetCompletions] = useState({});


    // pull the data from React Router DOM (from flashcard click)
    const location = useLocation();

    // set up sizes for responsive design
    const isMobile = useMediaQuery({ query: '(max-width: 950px)' });
    const isLandscape = useMediaQuery({ query: '(orientation: landscape)' });

    useEffect(()=>{
        if(!location.state) {
            // console.log("There is no state!")
            setIsData(false);
        }
        else{
            // console.log("There is state!");
            setIsData(true);
            setSetName(location.state.setName);
            setSetData(location.state.setCards);
            setSetPK(location.state.setPK);
            setSetCompletions(location.state.setCompletions);
        }
    }, [location.state]);

    // console.log(Object.keys(setData))
    // console.log("SetData: " + Object.keys(setData))

    return(
            <div style={{ height: "100%", width: "100%"}}>
            {!isData
                ? 
                    <div className="NoFlashcards">
                        No Flashcards loaded. Please select a flashcard set from the <Link to="/FlashcardLibrary">flashcard library</Link>
                    </div>
                :    
                    <div className="FlashcardCockpit"> 
                        {isMobile && isLandscape ?
                            <h6 className="NowStudying">Now studying: {setName}</h6>
                        :
                            <h3 className="NowStudying">Now studying: {setName}</h3>
                        }

                        <Flashcard name={setName} cards={setData} PK={setPK} completions={setCompletions} />

                    </div>   
            }
            </div>
        )

}

export default FlashcardStudySection;