import React from 'react';
import { Accordion } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';


import './HomeworkItem.css';
import '../../../assets/fonts/ProductSans/Product Sans Regular.ttf';

const HomeworkItem = (props) => {

    const homeworkData = props.data;
    const hwDataKeys = Object.keys(homeworkData);

    const isMobile = useMediaQuery({ query: '(max-width: 950px)' });
    const isLandscape = useMediaQuery({ query: '(orientation: landscape)' });

    const convertDate = (str) => {
        let titleString = '';
        let months = ["January","February","March","April","May","June","July","August","September","October","November","December"]
        let year = str.slice(0,4)
        let month = str.slice(5,7);
        let day = str.slice(8,10)
        
        let textMonth = months[parseInt(month)-1]
        
        titleString = year + ' ' + textMonth + ' ' + day;
        return(titleString)
    }

    // console.log(Object.keys(homeworkData));

    const mobileAccordionStyle = {
        width: "85vw",
    };

    const normalAccordionStyle = {
        width: "55vw",
    };


    return(
        <div className='HwAccordionElement' style={isMobile&&!isLandscape ? mobileAccordionStyle : normalAccordionStyle} >
            <Accordion>
                <Accordion.Item eventKey='1'>
                    <Accordion.Header>
                        <div className="HomeworkDate">
                            {convertDate(props.title)}
                        </div>                    
                    </Accordion.Header>
                    <Accordion.Body>
                        {/* <div dangerouslySetInnerHTML={{
                            __html: props.data
                        }} /> */}
                        {hwDataKeys.map((hwKey, idx)=>(
                            <div className="HomeworkDetails" key={idx} >
                                {/* <div className=""> */}
                                    <ul className="HomeworkItemUL">
                                        <li className="HomeworkItemLI1">{homeworkData[hwKey].HomeworkItem}</li>
                                        <li className="HomeworkItemLI2" type="none" >{homeworkData[hwKey].Description}</li>
                                    </ul>
                                {/* </div> */}
                            </div>
                        ))

                        }

                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
            {/* <Card>
                -----
            </Card> */}
        </div>
    )
}

export default HomeworkItem;