import React from 'react';
import { useMediaQuery } from 'react-responsive';

import './MediaQueries.css';

const MediaQueries = () => {

    const isDesktopOrLaptop = useMediaQuery({query: '(min-width: 1224px)' });
    const isBigScreen = useMediaQuery({ query: '(min-width: 1824px)' });
    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 1224px)' });
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' });
    const isRetina = useMediaQuery({ query: '(min-resolution: 2dppx)' });

    return(
        <div className="Entry">
            <div className="EntryTitle">
                <h2>Media Queries</h2>
            </div>
            <br />
            <div className="EntryPurpose">
                <b>Purpose: </b>
                <span>To find minimal and efficient media queries for use on the Homework app </span>
                <span>Use this page to determine which device someone is using</span>
            </div>
            <br />
            <div className="EntryReadme">
                <b>Use: </b>
                <span>The following has 3 specific divs: 'master', 'sub', and 'child'.</span>
                <span>Click the buttons for different messages in console log.</span>
                <span>Compare children to see how event delegation is handled differently. </span>
            </div>
            
            <div>
                <h1>Device Test!</h1>
                    {isDesktopOrLaptop && <p>You are a desktop or laptop</p>}
                    {isBigScreen && <p>You  have a huge screen</p>}
                    {isTabletOrMobile && <p>You are a tablet or mobile phone</p>}
                    <p>Your are in {isPortrait ? 'portrait' : 'landscape'} orientation</p>
                    {isRetina && <p>You are retina</p>}
            </div>
        </div>
    )
};

export default MediaQueries;