import React, { useEffect, useState } from 'react';
import { Carousel } from 'react-bootstrap';
import { useMediaQuery } from 'react-responsive';

import { getDatabase, ref, get, child } from "firebase/database";
import HomeworkItem from './HomeworkItem/HomeworkItem';

import './Homework.css';

const Homework = () => {

    const [isLoading, setIsLoading] = useState(false);
    const [hasBeenFetched, setHasBeenFetched] = useState(false);

    const [dbDates, setDbDates] = useState([]);
    const [dbEntries, setDbEntries] = useState([]);
    // const [dbQuery, setDbQuery] = useState({});  //all data --not needed for now
    const [uniqueDates, setUniqueDates] = useState([]);
    const [renderYearlyDates, setRenderYearlyDates] = useState([]);
    const [renderYearlyEntries, setRenderYearlyEntries] = useState([]);

    const [openCollapse, setOpenCollapse] = useState(false);
    // const [openCollapses, setOpenCollapses] = useState([]); // create 2023-09-19 for serial animations

    const [carouselSelection, setCarouselSelection] = useState(0);

    // const [checked, setChecked] = useState(false);


    let db= getDatabase();

    useEffect(()=>{
        let unique = [];
        let dates = [];
        let entries = [];
        
        const fetchData = async () => {
            setIsLoading(true);

            const dbRef = ref(getDatabase());
            await get(child(dbRef, 'HomeworkItems/'))
            .then((snapshot) => {
                if (snapshot.exists()) {
                    let queryData = snapshot.val();
                    for (let i in Object.keys(queryData)) {
                        unique = unique.concat(Object.keys(queryData)[i].slice(0,4));
                        dates = dates.concat(Object.keys(queryData)[i]);
                        entries = entries.concat(queryData[Object.keys(queryData)[i]]);
                    }
                    unique = [...new Set(unique)]; // remove all the duplicate years
                    setUniqueDates(unique.reverse()) // set to state
                    // setDbQuery(queryData); // not needed now
                    setDbDates(dates.reverse()); // move recent to the top
                    setDbEntries(entries.reverse()); //move recent to the top

                    // set up default year -- do this only on the first render
                    let defaultYear = unique[carouselSelection];
                    // console.log("Default Year: " + defaultYear)
                    let allDates = dates;
                    let allEntries = entries;
                    let tempDates = [];
                    let tempEntries = [];
                    let tempOpenCollapses = []
                    for (let i in allDates) {
                        tempOpenCollapses = tempOpenCollapses.concat(false);
                        if (allDates[i].slice(0,4) === defaultYear) {
                            
                            tempDates = tempDates.concat(allDates[i]);
                            tempEntries = tempEntries.concat(allEntries[i]);
                            setRenderYearlyDates(tempDates);
                            setRenderYearlyEntries(tempEntries);
                            
                        }
                        else {
                            //do nothing
                        }
                    }

                    setIsLoading(false);

                    //2023-09-19 set up dynamic serial animation variables
                    // console.log("tempOpenCollapses: " + tempOpenCollapses);
                    // setOpenCollapses(tempOpenCollapses);
                    setTimeout(function () {
                        setOpenCollapse(true);
                    }, 333);
                    
                } else {
                    console.log("No Data Available")
                }
            })
            .catch((error) => {
                console.error(error);
            })
        }
        
        // 2023-06-02 - only execute API call once!
        // Execute API call 
        if (hasBeenFetched) {
            // do nothing
        }
        else {
            fetchData();
            setHasBeenFetched(true);
        }  

    }, [db])

    const handleCarouselSelect = (e) => {
        // console.log("handleCarouselSelect:")
        // console.log(e)

        // processing for YEARS
        setOpenCollapse(false);
        let yearRelevantDates = [];
        let yearRelevantEntries=[];
        for (let i in dbDates) {
            if (dbDates[i].slice(0,4) === uniqueDates[e]) {
                // console.log(dbDates[i])
                // console.log(dbEntries[i])
                yearRelevantDates = yearRelevantDates.concat(dbDates[i])
                yearRelevantEntries = yearRelevantEntries.concat(dbEntries[i])

                setRenderYearlyDates(yearRelevantDates);
                setRenderYearlyEntries(yearRelevantEntries);
            }
            else {
                //do nothing
            }
        }

        setCarouselSelection(e);
        // setOpenCollapse(true);
    };



    // const handleClick = () => {
    //     console.log("handleClick:")
    //     console.log("Selected Date: " + uniqueDates[carouselSelection]);
    //     let yearRelevantDates = [];
    //     let yearRelevantEntries=[];
    //     for (let i in dbDates) {
    //         if (dbDates[i].slice(0,4) === uniqueDates[carouselSelection]) {
    //             console.log(dbDates[i])
    //             console.log(dbEntries[i])
    //             yearRelevantDates = yearRelevantDates.concat(dbDates[i])
    //             yearRelevantEntries = yearRelevantEntries.concat(dbEntries[i])
    //         }
    //         else {
    //             //do nothing
    //         }
    //     }
    // }

    // const handleAnimate = () => {
    //     setOpenCollapse(!openCollapse)
    // };

    const handleSlide = () => {
        setOpenCollapse(false)  
    };

    const handleSlid = () => {
        setOpenCollapse(true);
    };

    const isMobile = useMediaQuery({ query: '(max-width: 950px)' });
    const isPortrait = useMediaQuery({ query: '(orientation: portrait)' });

    const mobileCarouselItemStyle={
        width: "350px",
        height: "50px",
    };    
    const normalCarouselItemStyle={
        width: "850px",
        height: "50px",
    };

    const carouselDivStyle = {
        width: "100%",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center"
    };

    return(
        <div className="HomeworkContainer">
            <div className="SectionTitle">
                Homework
            </div>
            {/* <div className="HomeworkText">
                This section will eventually allow students to track their past and present homework.
            </div> */}

            <div classname="tempCarousel" style={{display: "flex", flexDirection: "column", justifyContent:"center", alignItems: "center", marginBottom: "33px" }}>
                <Carousel variant="dark" 
                          indicators={false} 
                          interval={null}
                          onSelect={handleCarouselSelect}
                          onSlide={handleSlide}
                          onSlid={handleSlid}
                          style={{margin: "0, auto", maxWidth: "850px"}}>
                    {uniqueDates.map((year, idx)=>(
                        <Carousel.Item key={idx} style={isMobile&&isPortrait ? mobileCarouselItemStyle : normalCarouselItemStyle}>
                            <div style={carouselDivStyle}>
                                <h2><i>{year}</i></h2>
                            </div>
                        </Carousel.Item>
                    ))}
                </Carousel>
            </div>


            {/* <div>
                <Button onClick={handleAnimate}>
                    Animate
                </Button>
            </div> */}

            <div className="HomeworkLoader">
                {isLoading ? (
                    <div className="loader">
                        <div className="inner one"></div>
                        <div className="inner two"></div>
                        <div className="inner three"></div>
                    </div>
                ) : null }
            </div>

            <div className="AllHomeworkComponents">
                {renderYearlyDates.map((date, idx)=>(
                    <div style={openCollapse ? 
                        {
                            transition: "opacity 1.5s",
                            // transition: `opacity ${idx*1200}ms`,
                            opacity: "1"
                        }
                        : 
                        {
                            // transition: "opacity 0.5s",
                            // transition: `opacity ${idx}s`,
                            opacity: "0",
                            pointerEvents: "none"
                        }
                    } key={idx}>
                        {/* <Collapse in={openCollapse}> */}
                        <HomeworkItem key={idx} title={date} data={renderYearlyEntries[idx]} />
                        {/* </Collapse> */}
                    </div>
                ))}
            </div>

            {/* <div>
                <Button onClick={handleClick}>
                    Show Data
                </Button>
            </div>
            
            <div className="AllHomeworkComponents">
                {dbDates.length>0 && dbDates.map((date, idx) => (
                   <HomeworkItem key={idx} title={date} data={dbEntries[idx]} />
                ))}
            </div> */}
        </div>
    )
};

export default Homework;